.halo-page {
  background-color: var(--warna-biru);
  width: 100vw;
  height: calc(var(--vh, 1vh) * 100);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: var(--warna-biru);
  font-size: 1rem;
}
@media screen and (min-width: 30rem) {
  .halo-page {
    font-size: 1.5rem;
  }
}
@media screen and (min-width: 40rem) {
  .halo-page {
    font-size: 2rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .halo-page {
    font-size: 2.5rem;
  }
}
.halo-page .halo-page-h1 {
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  font-size: 18vw;
  text-transform: lowercase;
  overflow: hidden;
}
.halo-page h2 {
  text-transform: uppercase;
  overflow: hidden;
  z-index: 2;
}
.halo-page h1 {
  text-transform: uppercase;
  overflow: hidden;
  z-index: 2;
}
.halo-page .bunder {
  position: absolute;
  background-color: var(--warna-cream);
  border-radius: 50%;
  width: 200rem;
  height: 200rem;
  z-index: 1;
}
.halo-page .bunder2 {
  position: absolute;
  border: var(--warna-cream) solid 2px;
  border-radius: 50%;
  width: 220rem;
  height: 220rem;
  z-index: 1;
}